<template>
  <div class="page-container">
    <div class="page-box">
      <div class="page-box-title">
        <h2>Admin - File upload test</h2>
      </div>
      <div class="page-box-content">
        <b-field class="file">
          <b-upload
            v-model="file"
            @input="handleFileUpload"
          >
            <a class="button is-primary">
              <base-icon
                iconRef="cloud-upload"
                position="left"
              />
              <span>Click to upload</span>
            </a>
          </b-upload>
          <span
            class="file-name"
            v-if="file"
          >
            {{ file.name }}
          </span>
        </b-field>
      </div>
      <div class="page-box-footer"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Admin",
  data() {
    return {
      file: null
    };
  },
  methods: {
    handleFileUpload(file) {
      console.log("File uploaded to client", file);
      this.$store.dispatch("uploadFile", { fileName: file.name, blob: file });
    }
  }
};
</script>


